import React, { useRef } from 'react'
import { useGLTF } from '@react-three/drei'
import {useFrame} from "@react-three/fiber";

export default function Model(props) {
    const group = useRef()
    const { nodes, materials } = useGLTF('/models/kalmarsLogoBlack.glb')

    useFrame(({ clock }) => {
        const t = (1 + Math.sin(clock.getElapsedTime() * 0.5)) / 100;
        group.current.rotation.y += t
    });

    return (
        <group ref={group} {...props} position={[0, 0, 0]} dispose={null}>
            <mesh
                castShadow
                receiveShadow
                geometry={nodes.Curve.geometry}
                material={materials['Material.001']}
                rotation={[1, 0, 0]}
                position={[-4, -2, 0]}
                scale={[131, 531, 131]}>
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve001.geometry}
                    material={nodes.Curve001.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve002.geometry}
                    material={nodes.Curve002.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve003.geometry}
                    material={nodes.Curve003.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve004.geometry}
                    material={nodes.Curve004.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve005.geometry}
                    material={nodes.Curve005.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve006.geometry}
                    material={nodes.Curve006.material}
                />
                <mesh
                    castShadow
                    receiveShadow
                    geometry={nodes.Curve007.geometry}
                    material={nodes.Curve007.material}
                />
            </mesh>
        </group>
    )
}

useGLTF.preload('/models/kalmarsLogoBlack.glb')
